    overflow: hidden;

    h2 {
      @include title(#000);
      writing-mode: vertical-lr;
      //position: absolute;
      //transform: rotate(270deg);
      //bottom: 0;
      // left: 0;
      // margin-left: -200px;
    }
  }
}



